@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700&display=swap);
/* 
--- 01 TYPOGRAPHY SYSTEM
- FONT SIZE SYSTEM (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

- FONT WEIGHTS
Default: 400
500 / 600 / 700

- LINE HEIGHTS
Default: 1

--- 02 WHITESPACE
- Spacing system (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128


--- 03 RESPONSIVE
mobile: 480px
tabet: 767px

*/

:root {
	/* Color */
	--primary-color: #5855ec;
	--dark-primary-color: #4f4dd4;
	--tint-color: #eeeefd;
	--grey-color: #555;
	--dark-grey-color: #333;
	--light-grey-color: #eee;
	--white-color: #fff;
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html {
	/* 10px / 16px = 0.625 */
	/* Percentage of user's browser font-size setting */
	font-size: 62.5%;
}

body {
	font-family: 'Rubik', sans-serif;
	line-height: 1;
	font-weight: 400;
	color: #555;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.nav-list {
	list-style: none;
	display: flex;
	align-items: center;
	grid-gap: 3.2rem;
	gap: 3.2rem;
}

.main-nav-link:link,
.main-nav-link:visited {
	display: inline-block;
	text-decoration: none;
	color: var(--dark-grey-color);
	font-weight: 400;
	font-size: 1.8rem;
	transition: all 0.3s;
}

.main-nav-link:hover,
.main-nav-link:active {
	color: var(--dark-primary-color);
}

.main-nav-link.nav-cta:link,
.main-nav-link.nav-cta:visited {
	padding: 1.2rem 2.4rem;
	border-radius: 10px;
	background-color: var(--primary-color);
	color: var(--white-color);
	text-transform: uppercase;
}

.main-nav-link.nav-cta:hover,
.main-nav-link.nav-cta:active {
	background-color: var(--dark-primary-color);
}

.side-drawer {
	position: fixed;
	right: 0;
	top: 0;
	z-index: 100;
	height: 100vh;
	width: 70%;
	background: white;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background: rgba(0, 0, 0, 0.75);
	z-index: 10;
}

/* NAVIGATION */

.main-navigation__menu-btn {
	display: none;
	font-size: 3.6rem;
	background: transparent;
	border: none;
}

.main-navigation__drawer-nav {
	height: 100%;
}

.mobile-nav-menu,
.mobile-nav-cancel {
	display: none;
	font-size: 3.6rem;
}

.mobile-nav {
	display: flex;
	flex-direction: column;
}

/* HEADER */
.header {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: var(--tint-color);
	box-shadow: 1 1.2rem 3.2rem rgba(0, 0, 0, 0.03);

	/* Because we want header to be sticky */
	/* Otherwise it should be padding */
	height: 9.6rem;
	padding: 0 4.8rem;
}
.logo:link,
.logo:visited {
	height: 2.2rem;
	text-decoration: none;
	color: var(--dark-grey-color);
}

.container {
	max-width: 120rem;
	margin: 0 auto;
	padding: 0 3.2rem;
}

.mode {
	box-shadow: 0 2.4rem 4.8rem rgba(0, 0, 0, 0.075);
	border-radius: 11px;
	overflow: hidden;
}

.mode-content {
	padding: 3.2rem 4.8rem 4.8rem 4.8rem;
}

.mode-title {
	display: flex;
	align-items: center;
	grid-gap: 1.2rem;
	gap: 1.2rem;
	font-size: 2.4rem;
	color: var(--dark-grey-color);
	font-weight: 600;
	text-transform: uppercase;
	margin-bottom: 3.2rem;
}

.mode-description {
	font-size: 1.8rem;
	margin-bottom: 3.2rem;
	line-height: 1.6;
}

.mode-try {
	color: var(--dark-grey-color);
	background-color: var(--light-grey-color);
}

.mode-attributes {
	list-style: none;
	display: flex;
	flex-direction: column;
	grid-gap: 2rem;
	gap: 2rem;
}

.modal {
	z-index: 100;
	position: fixed;
	top: 15vh;
	left: 10%;
	width: 80%;
	background: var(--tint-color);
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
	border-radius: 11px;
	overflow: hidden;
}

.modal__header {
	width: 100%;
	padding: 1rem 0.5rem;
	background: var(--primary-color);
	color: white;
}

.modal__header h2 {
	margin: 0.5rem;
	font-size: 2.4rem;
	text-transform: uppercase;
}

.modal__content {
	height: 500px;
	padding: 1rem 0.5rem;
}

.modal__footer {
	padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
	.modal {
		left: calc(50% - 20rem);
		width: 40rem;
	}
}

.modal-enter {
	transform: translateY(-10rem);
	opacity: 0;
}

.modal-enter-active {
	transform: translateY(0);
	opacity: 1;
	transition: all 200ms;
}

.modal-exit {
	transform: translateY(0);
	opacity: 1;
}

.modal-exit-active {
	transform: translateY(-10rem);
	opacity: 0;
	transition: all 200ms;
}

.button {
	display: flex;
	align-items: center;
	grid-gap: 0.8rem;
	gap: 0.8rem;
	font-size: 1.6rem;
	text-decoration: none;
	text-transform: uppercase;
	color: var(--dark-grey-color);
	background-color: var(--light-grey-color);
	border: none;
	padding: 1.2rem 2.4rem;
	border-radius: 11px;
	cursor: pointer;
	margin: auto;
}

.button--mode {
	display: flex;
	justify-content: center;
	width: 70%;
	padding: 1.5rem;
	border: 2px solid transparent;
	border-radius: 11px;
	background-color: var(--primary-color);
	color: var(--light-grey-color);
	text-transform: uppercase;
	font-size: 2.4rem;
	font-weight: 600;
}


.coin-container {
  text-align: center;
  margin: auto;
}

h1 {
  font-size: 22px;
  padding-top: 20px;
}

.headCoin,
.tailCoin {
  margin: 9.6rem auto;
  display: block;
}

.dice-container {
	padding: 4.8rem;
	text-align: center;
	font-size: 2.4rem;
}
.dice-buttons-group {
	margin: 2.4rem auto;
	width: 60%;
	display: flex;
	justify-content: center;
	grid-gap: 4.8rem;
	gap: 4.8rem;
}

.dice {
	line-height: 28vh;
	height: 28vh;
	font-size: 124px;
}

.spin {
	margin: auto;
	text-align: center;
}

.arrow {
	width: 35rem;
	display: block;
	margin: 3.2rem auto;
}

